/*------------------------------------*\
    $GLOBAL
\*------------------------------------*/
.header {
  padding: 40px 0;
  position: relative;
  z-index: 20; }
  .header .logo-right {
    font-family: proxima-nova, sans-serif;
    font-weight: 400;
    font-style: normal;
    color: #ffffff;
    font-size: 14px;
    text-align: right; }
    .header .logo-right span {
      display: inline-block;
      line-height: 1.6;
      font-size: 10px;
      margin-right: 5px; }
    .header .logo-right img {
      margin-top: -19px;
      display: inline-block; }

/*------------------------------------*\
    $COMPONENTS
\*------------------------------------*/
/*------------------------------------*\
    $BUTTONS
\*------------------------------------*/
.btn:hover {
  text-decoration: none; }

.btn:active, .btn:focus {
  outline: none; }

.btn--block {
  display: block !important;
  width: 100% !important; }

.button {
  background-color: #ffffff;
  display: inline-block;
  padding: 18px 60px;
  border-radius: 6px;
  text-transform: uppercase;
  text-align: center;
  color: #00a4c3 !important;
  font-weight: 300;
  margin-top: 30px;
  border: 1px solid #00a4c3;
  font-size: 12px;
  text-decoration: none !important; }
  .button:hover {
    background-color: #00a4c3;
    color: #ffffff !important; }

/*------------------------------------*\
    $FORMS
\*------------------------------------*/
form {
  margin: 0; }

.field-group {
  padding: 20px;
  background: #eaeaea;
  margin-bottom: 20px;
  border-radius: 5px;
  position: relative;
  height: auto; }

.validation {
  display: none; }

.has-error .validation {
  clear: both;
  display: block; }

input[type="text"],
input[type="email"],
input[type="number"],
.jscolor, .desc_input, select, .device-viewer textarea {
  float: left;
  height: auto;
  padding: 15px;
  height: 50px;
  width: 100% !important;
  border-radius: 0;
  color: #555;
  background-color: #e1e1e1;
  background-image: none;
  border: 1px solid #000000 !important;
  margin-right: 2%;
  font-size: 12px;
  outline: 0; }

.desc_input {
  height: 100px;
  resize: none; }

.form-item {
  position: relative; }

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0; }

.styled-radio {
  width: 20%;
  display: inline-block; }

.styled-checkbox, .styled-radio {
  height: 30px; }
  .styled-checkbox label, .styled-radio label {
    cursor: pointer;
    text-transform: uppercase;
    color: #00a4c3;
    text-indent: 30px;
    font-size: 12px;
    line-height: 25px;
    letter-spacing: 1px;
    position: absolute;
    width: 25px;
    height: 25px;
    background: #ffffff;
    border: 1px solid #132c46;
    border-radius: 50%; }
    .styled-checkbox label:after, .styled-radio label:after {
      border-radius: 50%;
      opacity: 0;
      content: '';
      position: absolute;
      width: 13px;
      height: 13px;
      background: #33b6cf;
      top: 5px;
      left: 5px; }

.styled-checkbox input[type=checkbox]:checked + label:after, .styled-radio input[type=radio]:checked + label:after, .styled-checkbox input[type=radio]:checked + label:after {
  opacity: 1; }

input[type=checkbox], input[type=radio] {
  visibility: hidden;
  position: absolute; }

