		padding: 0;
		width: auto;
	  }
	  .ge-mainControls .ge-wrapper.ge-fixed {
		position: fixed;
		z-index: 20;
		top: 0;
	  }
	  .ge-mainControls .ge-addRowGroup .btn.btn-primary {
		background: #f4f4f4;
		color: $dark-blue;
		border-color: #999;
